"use client";
import { Header } from "@my-ds/design-system";
// ۱. آیکون‌های مورد نیاز برای منو را وارد می‌کنیم
import { FiUser, FiLogOut } from "react-icons/fi";

export default function HomePage() {
  // ۲. آرایه‌ای از آیتم‌ها را برای منوی پروفایل تعریف می‌کنیم
  //    این آرایه دقیقاً با ساختار AvatarDropdownItem مطابقت دارد
  const profileItems = [
    {
      label: { text: "View Profile", size: "sm" },
      icon: <FiUser />,
      onClick: () => alert("Navigating to profile page..."),
    },
    {
      label: { text: "Logout", size: "sm", variant: "error" },
      icon: <FiLogOut />,
      onClick: () => alert("Logging out..."),
    },
  ];

  return (
    <div>
      <Header
        title="My Profile"
        showThemeSwitch={true}
        showNotificationButton={true}
        // ۳. به جای onAvatarClick، پراپرتی avatarDropdownItems را پاس می‌دهیم
        avatarDropdownItems={profileItems}
        // ۴. اطلاعات خود آواتار را همچنان از طریق userAvatar ارسال می‌کنیم
        userAvatar={{
          src: "https://i.pravatar.cc/150?u=raven",
          alt: "Raven's Avatar",
          size: "md", // سایز md برای هدر مناسب‌تر است
        }}
      />
      <main style={{ padding: "1rem" }} className="h-screen">
        <p>Page content goes here.</p>
        <p>Now when you click on the avatar, a dropdown menu will appear!</p>
      </main>
    </div>
  );
}